* {
    margin: 0;
    padding: 0;
}

.Todo{
    width: 100%;
    padding: 8px;
    background:  #f8f6f6;
    height: 440px;
}

.Todolist {
    padding: 15px;
    font-size: 18px;
    line-height: 30px;
    display: flex;

}

.Todo____list {
    /* width: 820px; */
    height: 560px;
    margin-left: 10px;
    font-size: 13px;
    border-radius: 2px;
    overflow: hidden;
    background: #fff;
    display: block;
}

.Todo__list {
    float: left;
    font-size: 13px;
    border-radius: 2px;
    min-height: 560px;
    background: #fff;
    display: block;
}

.left_l {
    width: 180px;
    display: block;
    padding: 15px;
    color:#000;
}

.left_l:hover{
    color: cornflowerblue;
    background: #ecf5ff;
}

.contact {
    font-size: 16px;
    margin-left: 17px;
    padding:8px 0px 0px 0px;
}

.contactor {
    display: flex;
}

.customer {
    margin: 0 30px 0 20px;
}

.custome {
    margin-right: 30px;
}

.custom {
    margin-top: 6px;
}