.ly2_10_contentbottom{
    width: 100%;
    height: 450px;
    overflow-x: scroll ;
}
.ly2_10_contentbottom::-webkit-scrollbar {
    height: 5px;
    background-color: #eee;
    border-radius: 1px;
}
.ly2_10_contentbottom::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 5px;
}
