* {
    margin: 0;
    padding: 0;
}

.Todo____list {
    height: 500px;
    margin-left: 10px;
    font-size: 13px;
    border-radius: 2px;
    overflow: hidden;
    background: #fff;
    display: block;
}

.has{
    overflow: hidden;
    color: #909399;
    font-weight: 200;
    float: right;
}
.has tr{
    flex: 1;
    width: 1110px;
    display: table-row;
    display: inline-block;
    vertical-align: inherit;
}
.has .el_table{
    padding: 12px 0;
    overflow: hidden;
    text-align: left;
    border-top: 1px solid #e6e6e6;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    white-space: nowrap;
    background-color: #fafdff;
    font-weight: 400;
    border-color: #e6e6e6;
    color: #475059;
}
.el_table .cell{
    display:block;
    overflow: hidden;
    text-overflow: ellipsis;
}
.el_table>.cell{
    overflow: hidden;
    word-break: break-all;
    line-height: 23px;
    padding-right: 10px;
    padding-left: 10px;
}

.Todo__list {
    float: left;
    font-size: 13px;
    border-radius: 2px;
    min-height: 500px;
    background: #fff;
    display: block;
    margin-right: 9px;
}

.left_l {
    width: 180px;
    display: block;
    padding: 15px;
    color:#000;
}

.left_l:hover{
    color: cornflowerblue;
    background: #ecf5ff;
}

.contact {
    font-size: 16px;
    margin-left: 17px;
    padding:8px 0px 0px 0px;
}

.contactor {
    display: flex;
}

.customer {
    margin: 0 30px 0 20px;
}

.custome {
    margin-right: 30px;
}

.custom {
    margin-top: 6px;
}

.ly2_2_contentbottom{
    width: 100%;
    height: 400px;
    overflow-x: scroll ;
}
.ly2_2_contentbottom::-webkit-scrollbar {
    height: 5px;
    background-color: #eee;
    border-radius: 1px;
}
.ly2_2_contentbottom::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 5px;
}
.ly2_2_contentbottom_box{
    width: 1610px;
    display: flex;
}
.ly2_2_top{
    width: 140px;
    height: 48px;
    font-size: 12px;
    padding: 0px 10px;
    background: #fafdff;
    line-height: 48px;
    border: 1px solid #ccc;
}