 /* 去除默认样式 */
 * {margin: 0;padding: 0;}
 .clearfix:after, .clearfix:before {content: '';display: table;}
 .clearfix:after {clear: both;}
 input {border: none;outline: none;background-color: transparent;}
 ul, ol {list-style-type: none;}
 a { text-decoration: none;}
 img {vertical-align: top;}
 em, i {font-style: normal;}


 .ly2_1_box{
     height: 1500px;
 }
 .nav_one a {
     color: #fff !important;
 }
 .conten {
     height: 100%;
     overflow-y: scroll;
 }
 .conten::-webkit-scrollbar {
     width: 5px;
     background-color: #eee;
     border-radius: 1px;
 }
 .conten::-webkit-scrollbar-thumb {
     background-color: #ccc;
     border-radius: 5px;
 }
 /* ly2_1 */
 .ly2_1_top {
     height: 40px;
     line-height: 40px;
     margin-top: 15px;
 }
 .ly2_1_top_left {
     float: left;
     margin-left: 15px;
 }
 .ly2_1_logo {
     width: 40px;
     height: 40px;
     margin-right: 20px;
 }
 .ly2_1_name {
     font-size: 16px;
 }
 .ly2_1_namefen {
     width: 1px;
     background-color: #aaa;
     height: 12px;
     margin: 14px;
 }
 .ly2_1_type {
     font-size: 12px;
     color: #3e84e9;
     margin-right: 15px;
 }
 /*  */
 .ly2_1_time {
     width: 200px;
     height: 36px;
     margin-top: 2px;
     overflow: 'hidden'
 }
 .ant-cascader-menu {
     width: 200px;
 }
 .ant-cascader-menu::-webkit-scrollbar {
     width: 5px;
     background-color: #eee;
     border-radius: 1px;
 }
 .ant-cascader-menu::-webkit-scrollbar-thumb {
     background-color: #ccc;
     border-radius: 5px;
 }
 .ant-cascader-menu-item-active {
     color: #409eff;
 }
 .ly2_1_top_left>div {
     float: left;
 }
 .ly2_1_top_right {
     float: right;
     margin-right: 20px;
 }
 /* 弹出框样式 */
 .ly2_1_top_item {
     overflow-y: scroll;
     height: 60vh;
 }
 .ly2_1_top_item::-webkit-scrollbar {
     width: 5px;
     background-color: #eee;
     border-radius: 1px;
 }
 .ly2_1_top_item::-webkit-scrollbar-thumb {
     background-color: #ccc;
     border-radius: 5px;
 }
 .ly2_1_top_item_input {
     width: 100%;
 }
 .ly2_1_top_item_input_box {
     width: 44%;
     margin: 10px 3%;
     float: left;
 }
 .ly2_1_top_item_input_box p {
     font-size: 13px;
     line-height: 20px;
     color: #333;
 }
 .ly2_1_top_item_input_box_box {
     width: 100%;
     border: #ddd solid 1px;
     border-radius: 3px;
     height: 34px;
     line-height: 34px;
     padding-left: 10px;
 }
 .ly2_1_top_item_input_box_box input {
     width: 100%;
     height: 100%;
 }
 .ly2_1_top_item_btn {
     width: 50px;
     height: 30px;
     margin: 15px 0% 15px 3%;
     background-color: #3e84e9;
     color: #fff;
     line-height: 30px;
     text-align: center;
     border-radius: 2px;
 }
 .ly2_1_top_mask {
     margin: 3%;
     border: #ccc solid 1px;
 }
 .mask_box {
     width: 100%;
     display: flex;
 }
 .mask_box_item {
     border-bottom: #ccc solid 1px;
     padding-left: 10px;
     font-size: 14px;
     color: #909399;
     line-height: 40px;
     flex: 1;
     border-right: #ccc solid 1px;
 }
 .mask_box .mask_box_item:nth-last-child(1) {
     border-right: none;
 }
 .mask_content {
     height: 300px;
 }
 .mask_content p {
     text-align: center;
     font-size: 14px;
     line-height: 20px;
     margin-top: 10px;
     color: #909399
 }
 .ly2_1_content {
     width: 100%;
 }
 .ly2_1_content_item {
     width: 47%;
     float: left;
     background-color: #fff;
