* {
    margin: 0;
    padding: 0;
}

.Todo____list {
    height: 500px;
    margin-left: 10px;
    font-size: 13px;
    border-radius: 2px;
    overflow: hidden;
    background: #fff;
    display: block;
}
.has{
    overflow: hidden;
    color: #909399;
    font-weight: 200;
    float: right;
}
.has .hsat{
    flex: 1;
    width: 1131px;
    display: table-row;
    display: inline-block;
    vertical-align: inherit;
}
.has .el_table{
    padding: 12px 0;
    overflow: hidden;
    text-align: center;
    border-top: 1px solid #e6e6e6;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    white-space: nowrap;
    background-color: #fafdff;
    font-weight: 400;
    border-color: #e6e6e6;
    color: #475059;
}
.el_table .cell{
    display:inline-block;
    overflow: hidden;
    line-height: 23px;
}
.Todo__list {
    float: left;
    font-size: 13px;
    border-radius: 2px;
    min-height: 500px;
    background: #fff;
    display: block;
    margin-right: 9px;
}

.left_l {
    width: 180px;
    display: block;
    padding: 15px;
    color:#000;
}

.left_l:hover{
    color: cornflowerblue;
    background: #ecf5ff;
}

.contact {
    font-size: 16px;
    margin-left: 17px;
    padding:8px 0px 0px 0px;
}

.contactor {
    display: flex;
}

.customer {
    margin: 0 30px 0 20px;
}

.custome {
    margin-right: 30px;
}

.custom {
    margin-top: 6px;
}